<template>
  <b-container>
    <h4>Activate User</h4>
    <b-form style="margin-top: 50px;" @submit="onGuardianActivate">
      <b-form-group label="Phone #: " label-align="left" label-for="phone-number-input" description="Phone # is for authentication purpose only">
        <b-form-input id="phone-number-input" v-model="phoneNum" placeholder="Enter Phone #" required @keyup="focusOut" @blur="focusOut"></b-form-input>
      </b-form-group>
      <b-form-group label="Reg. Code" label-align="left" label-for="reg-code-input" description="Please check your email for registration code">
        <b-form-input id="reg-code-input" v-model="regCode" placeholder="Enter Reg.Code" required @keyup="focusOut" @blur="focusOut"></b-form-input>
      </b-form-group>
      <b-row class="ml-1 mb-2">
        <b-button type="submit" variant="warning">Activate</b-button>
      </b-row>
    </b-form>
    <b-row class="ml-1">
      <b-button variant="primary" @click="$router.push('/mobile/login')">To Login Page</b-button>
    </b-row>
  </b-container>
</template>

<script>
import config from "../config";
// const moment = require('moment')
export default {
  name: "MobileActivate",
  data() {
    return {
      phoneValue: "",
      phoneNum: "",
      regCode: "",
      preventNextIteration: false,
    };
  },
  methods: {
    focusOut(event) {
      if (["Arrow", "Backspace", "Shift"].includes(event.key)) {
        this.preventNextIteration = true;
        return;
      }
      if (this.preventNextIteration) {
        this.preventNextIteration = false;
        return;
      }
      if (this.phoneNum.length > 0) {
        this.phoneValue = this.phoneNum
          .replace(/-/g, "")
          .match(/(\d{1,10})/g)[0];
      }
      // Format display value based on calculated currencyValue
      this.phoneNum = this.phoneValue.replace(
        /(\d{1,3})(\d{1,3})(\d{1,4})/g,
        "$1-$2-$3"
      );
    },
    onGuardianActivate() {
      var _this = this;
      this.activateGuardianInDb(
        () => {
          _this.loginGuardian();
        },
        () => {
          alert("Activation Failed:( - Try Another Reg. Code");
        }
      );
    },
    loginGuardian() {
      var _this = this;
      this.getFamilyByPhoneNumFromDb(
        (family) => {
          var guardian = _this.getGuardianInFamilyByPhoneNum(family);
          this.$store.commit("setLoggedInFamily", family);
          this.$store.commit("setLoggedInGuardianID", guardian._id);
          this.$router.push("/mobile/home");
        },
        () => {
          this.$router.push("/mobile/activate");
        }
      );
    },
    activateGuardianInDb(successCallback, activateFailedCallback) {
      // var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "guardian/activate";
      console.log(`activateGuaridanInDb - query: ${query}`)
      http.open("POST", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          if (successCallback != null) {
            successCallback();
          }
        } else if (this.readyState === 4) {
          var response = JSON.parse(this.responseText);
          if (response.message != undefined) {
            if (
              response.message.includes("Try Another") &&
              activateFailedCallback != null
            ) {
              activateFailedCallback();
              return;
            }
            alert(response.message);
            return;
          }
          alert(this.responseText);
        }
      };
      try {
        http.send(
          JSON.stringify({
            phone_num: this.phoneNum,
            reg_code: this.regCode,
          })
        );
      } catch (e) {
        alert(e);
      }
    },
    getFamilyByPhoneNumFromDb(successCallback, needRegCallback) {
      // var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "guardian/login";
      http.open("POST", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          var family = JSON.parse(this.responseText).data;
          if (family && successCallback != null) {
            successCallback(family);
          }
        } else if (this.readyState === 4) {
          var response = JSON.parse(this.responseText);
          if (response.message != undefined) {
            if (
              response.message.includes("Not Activated") &&
              needRegCallback != null
            ) {
              needRegCallback();
              return;
            }
            alert(response.message);
            return;
          }
          alert(this.responseText);
        }
      };
      try {
        http.send(
          JSON.stringify({
            phone_num: this.phoneNum,
          })
        );
      } catch (e) {
        alert(e);
      }
    },
    getGuardianInFamilyByPhoneNum(family) {
      for (const g of family.guardians) {
        if (g.phone_num === this.phoneNum) {
          return g;
        }
      }
      return "";
    },
  },
};
</script>