<template>
  <div>
    <b-navbar sticky type="dark" variant="dark">
      <b-navbar-brand href="#">CC Portal</b-navbar-brand>
      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown right>
          <!-- Using 'button-content' slot -->
          <template #button-content>
            <em> {{frasUsername}}</em>
          </template>
          <b-dropdown-item :to="'/portal/profile'">Profile</b-dropdown-item>
          <b-dropdown-item :to="'/portal/settings'">Settings</b-dropdown-item>
          <b-dropdown-item href="#">Sign Out</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-navbar>
    <div class="cc-sidebar">
      <b-nav vertical>
        <b-nav-item class="cc-sidebar-item" active :to="'/portal/cc-records'">CC Records</b-nav-item>
        <b-nav-item class="cc-sidebar-item" active :to="'/portal/wards'">Patients</b-nav-item>
        <b-nav-item class="cc-sidebar-item" active :to="'/portal/families/dashboard'">Families</b-nav-item>
        <b-nav-item class="cc-sidebar-item" active :to="'/portal/admins'">Admins</b-nav-item>
      </b-nav>
    </div>
    <div class="cc-content">
      <router-view v-if="activeUserLoaded"></router-view>
    </div>
  </div>
</template>

<script>
import config from "../config";
const queryString = require("query-string");

export default {
  name: "CCPortal",
  components: {},
  data() {
    return {
      frasUsername: config.FRAS_USERNAME,
      activeUser: null,
      activeUserLoaded: false,
    };
  },

  computed: {
    activeInstitutionId() {
      // console.log('CC-Portal: activeInstitutionId');
      if (this.activeUser === null) {
        return "";
      }
      return this.activeUser.institution_id;
    },
  },
  created() {
    console.log("CC-Portal: mounted!");
    this.getActiveUserFromDb();
  },

  methods: {
    getActiveUserFromDb() {
      // console.log('CC-Portal: getActiveUserFromDb() called!')
      var _this = this;
      //// get active user in database
      const http = new XMLHttpRequest();
      const queryParams = { frasUsername: this.frasUsername };
      const queryArgs = queryString.stringify(queryParams);

      const query = config.API_LOCATION + "admin?" + queryArgs;
      http.open("GET", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // console.log(this.responseText)
          if (this.responseText.length == 0) {
            return;
          }
          _this.activeUser = JSON.parse(this.responseText).data;
          _this.$store.commit(
            "setActiveUser",
            JSON.parse(this.responseText).data
          );
          _this.activeUserLoaded = true;
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
  },
};
</script>

<style>
.cc-sidebar {
  height: 100%;
  width: 200px;
  position: fixed !important;
  background-color: rgb(5, 57, 75);
}
.nav-link {
  color: rgb(162, 195, 235) !important;
}
.cc-content {
  margin-left: 200px;
  height: 90%;
}
html,
body {
  height: 100%;
}
</style>
