import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import BootstrapVue from "bootstrap-vue"
import VueRouter from 'vue-router'
import App from './App.vue'
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap-vue/dist/bootstrap-vue.css"
import routes from './router'

Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(VueRouter)
Vue.use(Vuex)

const router = new VueRouter({
  routes
})

const vuexSession = new VuexPersistence({
  storage: window.sessionStorage
})

const store = new Vuex.Store({
  state: {
    activeUser: null,
    loggedInFamily: null,
    loggedInGuardianID: "",
    loadedFamily: null,
  },
  mutations: {
    setActiveUser (state, u) {
      state.activeUser = u
    },
    setLoadedFamily (state, f) {
      state.loadedFamily = f
    },
    setLoggedInFamily (state, f) {
      state.loggedInFamily = f
    },
    setLoggedInGuardianID(state, g) {
      state.loggedInGuardianID = g
    },
    resetLoadedFamily (state) {
      console.log('loadedFamily reset to null')
      state.loadedFamily = null
    },
    resetLoggedInFamily (state) {
      console.log('loggedInFamily reset to null')
      state.loggedInFamily = null
    },
    resetLoggedInGuardianID (state) {
      console.log('loggedInGaurdian reset to empty')
      state.loggedInGuardianID = ""
    }
  },
  plugins: [vuexSession.plugin]
})

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
