import CCPortal from "../components/CCPortal"
import CCRecords from "../components/CCRecords";
import Families from "../components/Families";
import Wards from "../components/Wards";
import AddFamily from "../components/AddFamily";
import ViewFamilyInfo from "../components/ViewFamilyInfo";
import Admins from "../components/Admins";
import AdminProfile from "../components/AdminProfile"
import AdminSettings from "../components/AdminSettings"
import Mobile from "../components/Mobile"
import MobileLogin from "../components/MobileLogin"
import MobileActivate from "../components/MobileActivate"
import MobileContainer from "../components/MobileContainer"
import AddInstitution from "../components/AddInstitution"
import AddAdmin from "../components/AddAdmin"
import RedirectCCPortal from "../components/RedirectCCPortal"
const routes = [
  {
    path: "/portal",
    name: "CCPortal",
    component: CCPortal,
    children: [
      {
        path: "/portal/cc-records",
        component: CCRecords,
      },
      {
        path: "/portal/families/dashboard",
        component: Families,
      },
      {
        path: "/portal/wards/",
        component: Wards,
      },
      {
        path: `/portal/families/add-family`,
        component: AddFamily,
      },
      {
        path: `/portal/families/info`,
        component: ViewFamilyInfo,
      },
      {
        path: "/portal/admins",
        component: Admins,
      },
      {
        path: "/portal/profile",
        component: AdminProfile,
      },
      {
        path: "/portal/settings",
        component: AdminSettings,
      },
    ]
  },
  {
    path: "/mobile",
    name: "Mobile",
    component: MobileContainer,
    children: [
      {
        path: "/mobile/home",
        component: Mobile,
      },
      {
        path:"/mobile/activate",
        component: MobileActivate,
      },
      {
        path:"/mobile/login",
        component: MobileLogin,
      }
    ]
  },
  {
    path: "/super-admin/add-institution",
    name: "AddInstitution",
    component: AddInstitution,
  },
  {
    path: "/super-admin/add-admin",
    name: "AddAdmin",
    component: AddAdmin,
  },
  {
    path: "/super-admin/redirect",
    name: "RedirectCCPortal",
    component: RedirectCCPortal,
  }
];

export default routes;
