<template>
  <b-container>
    <h3>Family Information</h3>

    <b-card title="Guardians" align="left">
      <template #header>
        <b-row align-h="end">
          <b-button @click="onCreateGuardian()">Add Guardian</b-button>
        </b-row>
      </template>
      <b-table bordered :items="gItems" :fields="gFields" stacked="md">
        <template #cell(actions)="row">
          <b-link class="mr-2" style="color:green" @click="onCheckRegCode(row.item)">
            <b>Reg.Code</b>
          </b-link>
          <b-link class="mr-2" style="color:blue" @click="onEditGuardian(row.item)">
            <b>Edit</b>
          </b-link>
          <b-link class="mr-1" style="color:red" @click="onDeleteGuardian(row.item)">
            <b>Delete</b>
          </b-link>
        </template>
      </b-table>
    </b-card>
    <b-card title="Vehicles" align="left">
      <template #header>
        <b-row align-h="end">
          <b-button @click="onCreateVehicle()">Add Vehicle</b-button>
        </b-row>
      </template>
      <b-table bordered :items="vItems" :fields="vFields">
        <template #cell(actions)="row">
          <b-link class="mr-2" style="color:blue" @click="onEditVehicle(row.item)">
            <b>Edit</b>
          </b-link>
          <b-link class="mr-1" style="color:red" @click="onDeleteVehicle(row.item)">
            <b>Delete</b>
          </b-link>
        </template>
      </b-table>
    </b-card>
    <b-card title="Patients" align="left">
      <template #header>
        <b-row align-h="end">
          <b-button @click="onCreateWard()">Add Patient</b-button>
        </b-row>
      </template>
      <b-table bordered :items="wItems" :fields="wFields">
        <template #cell(actions)="row">
          <b-link class="mr-2" style="color:blue" @click="onEditWard(row.item)">
            <b>Edit</b>
          </b-link>
          <b-link class="mr-1" style="color:red" @click="onDeleteWard(row.item)">
            <b>Delete</b>
          </b-link>
        </template>
      </b-table>
    </b-card>
    <b-modal ref="reg-code-modal" hide-footer title="Check Registration Code">
      <div class="d-block test-left ml-4 mb-2">
        Registration Code for
        <b>{{gModalItem.first_name + " " + gModalItem.last_name}}</b> is
        <b>{{displayRegCode}}</b>
      </div>
      <b-form>
        <b-input-group class="mb-2" prepend="Phone #">
          <b-form-input id="regcode-phone-input" v-model="regCodePhoneNum" placeholder="xxx-xxx-xxxx"></b-form-input>
        </b-input-group>
      </b-form>
      <b-row class="mr-1" align-h="end">
        <b-button variant="primary" @click="onSendRegCode">Send RegCode</b-button>
      </b-row>
    </b-modal>
    <b-modal ref="guardian-modal" hide-footer title="Edit Guardian">
      <div class="d-block text-left">
        <h3>{{gModalAction}} Guardian</h3>
      </div>
      <b-form>
        <b-form-row class="mb-2 mr-2">
          <b-col sm="6">
            <b-input-group prepend="First Name">
              <b-form-input id="guardian-input-first-name'" class="mb-2 mr-sm-2 mb-sm-0" placeholder="???" v-model="gModalItem.first_name"></b-form-input>
            </b-input-group>
          </b-col>
          <b-col sm="6">
            <b-input-group prepend="Last Name">
              <b-form-input id="guardian-input-last-name" placeholder="???" v-model="gModalItem.last_name"></b-form-input>
            </b-input-group>
          </b-col>
        </b-form-row>
        <b-input-group class="mb-2" prepend="Email">
          <b-form-input id="guardian-input-email" class="mb-2 mr-sm-2 mb-sm-0" placeholder="???" v-model="gModalItem.email"></b-form-input>
        </b-input-group>
        <b-form-row class="mb-2 mr-2">
          <b-col sm="6">
            <b-input-group prepend="Phone #">
              <b-form-input id="guardian-input-phone-number" class="mb-2 mr-sm-2 mb-sm-0" placeholder="???" v-model="gModalItem.phone"></b-form-input>
            </b-input-group>
          </b-col>
          <b-col sm="6">
            <b-input-group prepend="Relation">
              <b-form-input id="guardian-input-relation" placeholder="???" v-model="gModalItem.relation"></b-form-input>
            </b-input-group>
          </b-col>
        </b-form-row>
        <b-row class="mr-2" align-h="end">
          <b-button variant="primary" @click="onGuardianFormSubmit">{{gModalAction}}</b-button>
        </b-row>
      </b-form>
    </b-modal>
    <b-modal ref="vehicle-modal" hide-footer title="Edit Vehicle">
      <div class="d-block text-left">
        <h3>{{vModalAction}} Vehicle</h3>
      </div>
      <b-form>
        <b-form-row class="mb-2 mr-2">
          <b-col sm="6">
            <b-input-group prepend="Make">
              <b-form-input id="vehicle-input-make'" class="mb-2 mr-sm-2 mb-sm-0" placeholder="???" v-model="vModalItem.make"></b-form-input>
            </b-input-group>
          </b-col>
          <b-col sm="6">
            <b-input-group prepend="Model">
              <b-form-input id="vehicle-input-model" placeholder="???" v-model="vModalItem.model"></b-form-input>
            </b-input-group>
          </b-col>
        </b-form-row>
        <b-form-row class="mb-2 mr-2">
          <b-col sm="6">
            <b-input-group prepend="Color">
              <b-form-input id="vehicle-input-color'" class="mb-2 mr-sm-2 mb-sm-0" placeholder="???" v-model="vModalItem.color"></b-form-input>
            </b-input-group>
          </b-col>
          <b-col sm="6">
            <b-input-group prepend="Plate #">
              <b-form-input id="vehicle-input-plate" placeholder="???" v-model="vModalItem.plate_number"></b-form-input>
            </b-input-group>
          </b-col>
        </b-form-row>
        <b-row class="mr-2" align-h="end">
          <b-button variant="primary" @click="onVechileFormSubmit">{{vModalAction}}</b-button>
        </b-row>
      </b-form>
    </b-modal>
    <b-modal ref="ward-modal" hide-footer title="Edit Ward">
      <div class="d-block text-left">
        <h3>{{wModalAction}} Ward</h3>
      </div>
      <b-form>
        <b-form-row class="mb-2 mr-2">
          <b-col sm="6">
            <b-input-group prepend="First Name">
              <b-form-input id="ward-input-first-name'" class="mb-2 mr-sm-2 mb-sm-0" placeholder="???" v-model="wModalItem.first_name"></b-form-input>
            </b-input-group>
          </b-col>
          <b-col sm="6">
            <b-input-group prepend="Last Name">
              <b-form-input id="ward-input-last-name" placeholder="???" v-model="wModalItem.last_name"></b-form-input>
            </b-input-group>
          </b-col>
        </b-form-row>
        <b-form-row>
          <b-col sm="4">
            <b-input-group prepend="Group">
              <b-form-input id="ward-input-group" placeholder="???" v-model="wModalItem.group"></b-form-input>
            </b-input-group>
          </b-col>
        </b-form-row>
        <b-row class="mr-2" align-h="end">
          <b-button variant="primary" @click="onWardFormSubmit">{{wModalAction}}</b-button>
        </b-row>
      </b-form>
    </b-modal>
  </b-container>
</template>
<script>
import config from '../config'
const queryString = require("query-string");
export default {
  name: "ViewFamilyInfo",

  data() {
    var gModalItem = this.getNewGuardianModalItem();
    var vModalItem = this.getNewVehicleModalItem();
    var wModalItem = this.getNewWardModalItem();
    return {
      loadedFamily: null,
      // Action State
      gModalAction: "Update",
      vModalAction: "Update",
      wModalAction: "Update",
      // Table Items
      gItems: [],
      vItems: [],
      wItems: [],
      // Table Fields
      gFields: [
        "guardian_name",
        "phone",
        "email",
        "relation",
        "reg_code_sent",
        "status",
        "actions",
      ],
      vFields: ["make", "model", "color", "plate_number", "actions"],
      wFields: ["name", "group", "actions"],
      // Pop-Up Window Item
      gModalItem: gModalItem,
      vModalItem: vModalItem,
      wModalItem: wModalItem,
      currentRegCode: null,
      regCodePhoneNum: "",
    };
  },
  mounted() {
    this.loadedFamily = this.$store.state.loadedFamily;
    if (this.loadedFamily === null) {
      this.resetGuardianVariables();
      this.resetVehicleVariables();
      this.resetWardVariables();
      return;
    }
    this.processLoadedFamily();
  },
  computed: {
    displayRegCode() {
      if (this.currentRegCode === null) return "";
      return this.currentRegCode.reg_code;
    },
  },

  watch: {
    // loadedFamilyId: function () {
    //   // reset everything to empty if loaded family is null
    //   console.log(`loaded Family watcher called!`)
    //   if (this.loadedFamilyId === "") {
    //     this.resetGuardianVariables();
    //     this.resetVehicleVariables();
    //     this.resetWardVariables();
    //     return;
    //   }
    //   this.loadedFamily = this.$store.state.loadedFamily
    //   var _this = this;
    //   this.gItems = this.loadedFamily.guardians.map((guardian) => {
    //     return _this.mapGuardianToItem(guardian);
    //   });
    //   // this.vItems = loadedFamily.vehicles
    //   this.wItems = this.loadedFamily.wards;
    // },
  },

  methods: {
    mapGuardianToItem(guardian) {
      var reg_code_sent = guardian.status === 0 ? false : true;
      var regCodeSentDisplay = reg_code_sent ? "YES" : "NO";
      var first_name = guardian.first_name;
      var last_name = guardian.last_name;
      var statusDisplay = guardian.status === 2 ? "activated" : "assigned";
      return {
        _id: guardian._id,
        guardian_name: first_name + " " + last_name,
        phone: guardian.phone_num,
        email: guardian.email,
        relation: guardian.relation,
        device_id: guardian.device_id,
        reg_code_sent: regCodeSentDisplay,
        first_name: first_name,
        last_name: last_name,
        status: statusDisplay,
      };
    },
    mapVehicleToItem(vehicle) {
      return {
        _id: vehicle._id,
        make: vehicle.make,
        model: vehicle.model,
        color: vehicle.color,
        plate_number: vehicle.plate_num,
      }
    },
    mapWardToItem(ward) {
      var first_name = ward.first_name;
      var last_name = ward.last_name;
      return {
        _id: ward._id,
        name: first_name + " " + last_name,
        first_name: first_name,
        last_name: last_name,
        group: ward.group,
      }
    },
    onCheckRegCode(gItem) {
      // console.log(`gItem - ${JSON.stringify(gItem)}`);
      this.gModalItem = gItem;
      this.getRegCodeByGuardianIDFromDb();
      this.regCodePhoneNum = gItem.phone;
      this.$refs["reg-code-modal"].show();
    },
    onSendRegCode() {
      this.sendRegCodeWithSMS();
      this.$refs["reg-code-modal"].hide();
    },
    // Guardian Handlers
    onCreateGuardian() {
      this.gModalAction = "Create";
      this.gModalItem = this.getNewGuardianModalItem();
      this.$refs["guardian-modal"].show();
    },
    onEditGuardian(gItem) {
      console.log(`gItem - ${JSON.stringify(gItem)}`);
      this.gModalAction = "Update";
      this.gModalItem = gItem;
      this.$refs["guardian-modal"].show();
    },
    onGuardianFormSubmit() {
      if (this.gModalAction === "Update") {
        this.updateGuardianToDb();
      } else if (this.gModalAction === "Create") {
        this.addNewGuardianToDb();
      }
    },
    onDeleteGuardian(gItem) {
      // console.log(`gItem - ${JSON.stringify(gItem)}`);
      this.gModalItem = gItem;
      this.deleteGuardianInDb();
    },
    // Vehicle Handlers
    onCreateVehicle() {
      this.vModalAction = "Create";
      this.vModalItem = this.getNewVehicleModalItem();
      this.$refs["vehicle-modal"].show();
    },
    onEditVehicle(vItem) {
      this.vModalAction = "Update";
      this.vModalItem = vItem;
      this.$refs["vehicle-modal"].show();
    },
    onVechileFormSubmit() {
      if (this.vModalAction === "Update") {
        this.updateVehicleToDb();
      } else if (this.vModalAction === "Create") {
        this.addNewVehicleToDb();
      }
    },
    onDeleteVehicle(vItem) {
      this.vModalItem = vItem;
      this.deleteVehilceInDb();
    },
    // Ward Handlers
    onCreateWard() {
      this.wModalAction = "Create";
      this.wModalItem = this.getNewWardModalItem();
      this.$refs["ward-modal"].show();
    },
    onEditWard(wItem) {
      this.wModalAction = "Update";
      this.wModalItem = wItem;
      this.$refs["ward-modal"].show();
      
    },
    onWardFormSubmit() {
      if (this.wModalAction === "Update") {
        this.updateWardToDb();
      } else if (this.wModalAction === "Create") {
        this.addNewWardToDb();
      }
    },
    onDeleteWard(wItem) {
      this.wModalItem = wItem;
      this.deleteWardInDb();
    },
    // RegCode requests
    getRegCodeByGuardianIDFromDb() {
      var _this = this;
      const http = new XMLHttpRequest();
      const queryParams = { guardianID: this.gModalItem._id };
      const queryArgs = queryString.stringify(queryParams);
      const query = config.API_LOCATION + "reg-code?" + queryArgs;
      http.open("GET", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // var response = JSON.parse(this.responseText);
          console.log("get regcode succeed!");
          _this.currentRegCode = JSON.parse(this.responseText).data;
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
    sendRegCodeWithSMS() {
      var _this = this;
      const http = new XMLHttpRequest();
      const requestBody = {
        id: this.currentRegCode._id,
        phone_num: this.regCodePhoneNum,
        first_name: this.gModalItem.first_name,
      };
      const query = config.API_LOCATION + "reg-code/sms";
      http.open("POST", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          var response = JSON.parse(this.responseText);
          _this.getFamilyByIdFromDb();
          console.log("send regcode with SMS succeed!");
          if (response.message != undefined) {
            _this.$bvToast.toast(response.message, {
              title: "DataBase Message",
              autoHideDelay: 2000,
            });
          }
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(requestBody));
      } catch (e) {
        alert(e);
      }
    },
    // Guaridan requests
    addNewGuardianToDb() {
      var guardianToSubmit = this.getGuardianToSubmit();
      var _this = this;
      const http = new XMLHttpRequest();
      const queryParams = { familyID: this.loadedFamily._id };
      const queryArgs = queryString.stringify(queryParams);
      const query = config.API_LOCATION +  "guardian/add-new?" + queryArgs;
      http.open("POST", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 201) {
          // var response = JSON.parse(this.responseText);
          console.log("create succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(guardianToSubmit));
      } catch (e) {
        alert(e);
      }
    },
    updateGuardianToDb() {
      var guardianToSubmit = this.getGuardianToSubmit();
      var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "guardian/" + this.gModalItem._id;
      http.open("PUT", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // var response = JSON.parse(this.responseText);
          console.log("update succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(guardianToSubmit));
      } catch (e) {
        alert(e);
      }
    },
    deleteGuardianInDb() {
      var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "guardian/" + this.gModalItem._id;
      http.open("DELETE", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // var response = JSON.parse(this.responseText);
          console.log("delete succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
    // Vehicle Requests
    addNewVehicleToDb() {
      var vehicleToSubmit = this.getVehicleToSubmit();
      var _this = this;
      const http = new XMLHttpRequest();
      const queryParams = { familyID: this.loadedFamily._id };
      const queryArgs = queryString.stringify(queryParams);
      const query = config.API_LOCATION + "vehicle/add-new?" + queryArgs;
      http.open("POST", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 201) {
          // var response = JSON.parse(this.responseText);
          console.log("create succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(vehicleToSubmit));
      } catch (e) {
        alert(e);
      }
    },
    updateVehicleToDb() {
      var vehicleToSubmit = this.getVehicleToSubmit();
      var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "vehicle/" + this.vModalItem._id;
      http.open("PUT", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // var response = JSON.parse(this.responseText);
          console.log("update succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(vehicleToSubmit));
      } catch (e) {
        alert(e);
      }
    },
    deleteVehilceInDb() {
      var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "vehicle/" + this.vModalItem._id;
      http.open("DELETE", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // var response = JSON.parse(this.responseText);
          console.log("delete succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
    // Ward Requests
    addNewWardToDb(){
      var wardToSubmit = this.getWardToSubmit();
      var _this = this;
      const http = new XMLHttpRequest();
      const queryParams = { familyID: this.loadedFamily._id };
      const queryArgs = queryString.stringify(queryParams);
      const query = config.API_LOCATION + "ward/add-new?" + queryArgs;
      http.open("POST", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 201) {
          // var response = JSON.parse(this.responseText);
          console.log("create succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(wardToSubmit));
      } catch (e) {
        alert(e);
      }
    },
    updateWardToDb(){
      var wardToSubmit = this.getWardToSubmit();
      var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "ward/" + this.wModalItem._id;
      http.open("PUT", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // var response = JSON.parse(this.responseText);
          console.log("update succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(wardToSubmit));
      } catch (e) {
        alert(e);
      }
    },
    deleteWardInDb() {
      var _this = this;
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "ward/" + this.wModalItem._id;
      http.open("DELETE", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // var response = JSON.parse(this.responseText);
          console.log("delete succeed!");
          _this.getFamilyByIdFromDb();
          _this.$bvToast.toast(this.responseText, {
            title: "DataBase Message",
            autoHideDelay: 2000,
          });
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
    // Family Requests
    getFamilyByIdFromDb() {
      var _this = this;
      const http = new XMLHttpRequest();
      if (this.loadedFamily === null) {
        console.log("getFamilyByIdFromDb - loadedFamily is NULL!");
        return;
      }
      var query = config.API_LOCATION + "family/" + this.loadedFamily._id;
      http.open("GET", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // console.log(this.responseText);
          //// if data received with no error, add to table
          _this.loadedFamily = JSON.parse(this.responseText).data;
          _this.$store.commit("setLoadedFamily", _this.loadedFamily);
          // console.log(JSON.stringify(_this.loadedFamily));
          if (_this.loadedFamily) {
            _this.processLoadedFamily();
          }
          // console.log("Event_Table: items: " + JSON.stringify(this.items));
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
    // Guardian Util functions
    getGuardianToSubmit() {
      return {
        phone_num: this.gModalItem.phone,
        email: this.gModalItem.email,
        device_id: this.gModalItem.device_id,
        first_name: this.gModalItem.first_name,
        last_name: this.gModalItem.last_name,
        relation: this.gModalItem.relation,
      };
    },
    getNewGuardianModalItem() {
      return {
        phone: "",
        email: "",
        device_id: "",
        first_name: "",
        last_name: "",
        relation: "",
      };
    },
    // Vehicle Util functions
    getVehicleToSubmit() {
      return {
        make: this.vModalItem.make,
        model: this.vModalItem.model,
        color: this.vModalItem.color,
        plate_num: this.vModalItem.plate_number,
      };
    },
    getNewVehicleModalItem() {
      return {
        make: "",
        model: "",
        color: "",
        plate_number: "",
      };
    },
    // Ward Util functions
    getWardToSubmit() {
      return {
        first_name: this.wModalItem.first_name,
        last_name: this.wModalItem.last_name,
        group: this.wModalItem.group,
      }
    },
    getNewWardModalItem() {
      return {
        first_name:"",
        last_name:"",
        group:"",
      }
    },

    resetGuardianVariables() {
      this.gItems = [];
      this.gModalItem = this.getNewGuardianModalItem();
    },
    resetVehicleVariables() {
      this.vItems = [];
      this.vModalItem = this.getNewVehicleModalItem();
      // this.gModalItem = this.getNewGuardianModalItem()
    },
    resetWardVariables() {
      this.wItems = [];
      this.wModalItem = this.getNewWardModalItem();
      // this.gModalItem = this.getNewGuardianModalItem()
    },
    processLoadedFamily() {
      var _this = this;
      this.gItems = this.loadedFamily.guardians.map((guardian) => {
        return _this.mapGuardianToItem(guardian);
      });
      this.vItems = this.loadedFamily.vehicles.map((vehicle) => {
        return _this.mapVehicleToItem(vehicle);
      });
      this.wItems = this.loadedFamily.wards.map((ward) => {
        return _this.mapWardToItem(ward);
      })
      // this.vItems = loadedFamily.vehicles
      // this.wItems = this.loadedFamily.wards;
    },
  },
};
</script>