<template>
  <div>
    <b-navbar class="mb-2" sticky type="dark" variant="dark">
      <b-navbar-brand href="#">
        <img src="../assets/mAIRobotics Logo White Transparent-h30.png" alt />
      </b-navbar-brand>
      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown v-if="showUser" right>
          <!-- Using 'button-content' slot -->
          <template #button-content>
            <em>{{loggedInGuardianName}}</em>
          </template>
          <b-dropdown-item @click="onSignOut">Sign Out</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-navbar>
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: "MobileContainer",
  data() {
    return {
      showUser: false,
      loggedInFamily: null,
      loggedInGuardianID: "",
    };
  },
  computed: {
    loggedInGuardianName() {
      if (!this.loggedInFamily) {
        return "";
      }
      for (const g of this.loggedInFamily.guardians) {
        if (g._id === this.loggedInGuardianID) {
          return g.first_name;
        }
      }
      return "";
    },
  },
  watch: {
    /* eslint-disable */
    $route(to, from) {
      this.loggedInFamily = this.$store.state.loggedInFamily;
      this.loggedInGuardianID = this.$store.state.loggedInGuardianID;

      if (this.loggedInFamily == null) {
        this.showUser = false;
        return;
      }
      this.showUser = true;
    },
    /* eslint-enable */
  },
  methods: {
    onSignOut() {
      console.log(`onSignOut`);
      this.$store.commit("resetLoggedInFamily");
      this.$store.commit("resetLoggedInGuardianID");
      this.$router.push("/mobile/login")
    },
  },
};
</script>