<template>
  <b-container class="h-100 ml-2 mr-2" fluid>
    <b-row class="ml-2 mt-4" style="margin-bottom: 30px;">
      <h2>Dashboard</h2>
    </b-row>
    <b-row class="ml-2">
    <b-alert variant="success" :show="dismissCountDown" dismissible @dismissed="dismissCountDown=0" @dismiss-count-down="countDownChanged">
      # of Scan Records Today: <b>{{numTodayRecords}}</b>
    </b-alert>
    </b-row>
    <b-card class="h-100">
      <b-container class="h-100" fluid>
        <b-row>
          <b-col xl="8" lg="10">
            <b-form-group class="mb-0" label="Select A Date Range" label-for="sort-by-select" label-cols-sm="3" label-align-sm="right" label-size="sm">
              <b-input-group>
                <b-form-datepicker
                  class="ml-2"
                  id="datepicker-start"
                  placeholder="Choose a Start Date"
                  v-model="startDate"
                  :date-format-options="{ year: 'numeric', month: 'numeric', day: 'numeric' }"
                  locale="en"
                ></b-form-datepicker>
                <p class="ml-2">to</p>
                <b-form-datepicker
                  class="ml-2"
                  id="datepicker-end"
                  placeholder="Choose a End Date"
                  v-model="endDate"
                  :date-format-options="{ year: 'numeric', month: 'numeric', day: 'numeric' }"
                  locale="en"
                ></b-form-datepicker>
                <b-button class="ml-2" variant="primary" :disabled="!hasDateRanges" @click="onApplyDateRangeFilter">Filter</b-button>
              </b-input-group>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col xl="2" lg="4" class="my-1">
            <b-form-group label="Group" label-for="group-filter-input" label-cols-sm="5" label-align-sm="right" label-size="sm">
              <b-input-group>
                <b-form-input id="group-filter-input" v-model="wardGroupFilter" type="search" placeholder="Type to Search"></b-form-input>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col xl="3" lg="4" class="my-1">
            <b-form-group label="Temp Threshold" label-for="temp-threshold-filter-input" label-cols-sm="5" label-align-sm="right" label-size="sm" class="mb-0">
              <b-input-group>
                <b-input-group-prepend>
                  <b-form-checkbox v-model="enableTempThrdFilter"></b-form-checkbox>
                </b-input-group-prepend>
                <b-form-input type="number" step="0.1" id="temp-threshold-filter-input" v-model="tempThrdFilter" placeholder="99.2"></b-form-input>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col xl="2" lg="4" class="my-1">
            <b-form-group label="Include Expired" label-for="temp-threshold-filter-input" label-cols-sm="8" label-align-sm="right" label-size="sm" class="mb-0">
              <b-input-group>
                <b-form-checkbox id="include-expired-filter-input" v-model="includeExpiredFilter"></b-form-checkbox>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col xl="5" lg="12" class="my-1" align-self="end">
            <b-row align-h="end">
              <b-button class="mr-2 mb-2" variant="success" @click="onRefresh">Refresh</b-button>
              <b-button class="mr-2 mb-2" variant="info" :disabled="disableSingleAction" @click="onViewFamily">View Family</b-button>
              <b-button v-if="false" class="mr-2 mb-2" variant="primary" :disabled="disableManyAction || disableCheckout" @click="onCheckOutEvents">CheckOut</b-button>
              <b-button class="mr-2 mb-2" variant="danger" :disabled="disableManyAction" @click="onDeleteEvents">Delete</b-button>
              <b-button class="mr-2 mb-2" variant="outline-secondary" :disabled="disableManyAction" @click="onClearSelect">Clear Select</b-button>
            </b-row>
          </b-col>
        </b-row>
        <b-table
          bordered
          sticky-header="80%"
          selectable
          id="cc-record-table"
          ref="ccRecordTable"
          responsive="sm"
          :items="items"
          :fields="fields"
          :current-page="currentPage"
          :per-page="perPage"
          :filter="filter"
          :filter-included-fields="filterOn"
          :filter-function="filterFunction"
          @row-selected="onRowSelected"
          @filtered="onFiltered"
        ></b-table>
        <b-pagination v-model="currentPage" :total-rows="numRows" :per-page="perPage" aria-controls="cc-record-table"></b-pagination>
      </b-container>
    </b-card>
  </b-container>
</template>

<script>
import config from '../config'
const queryString = require("query-string");
const moment = require("moment");
const DATE_FORMAT_DISP = "MM/DD/YYYY";
const DATE_FORMAT = "YYYY-MM-DD";
const TIME_FORMAT = "hh:mm A";
export default {
  name: "CCRecords",
  data() {
    return {
      instId: "",
      currentPage: 1,
      perPage: 20,
      ccRecords: [],
      fields: [
        "date",
        "patient_name",
        "group",
        "guardian_name",
        "temperature",
        "phone_number",
        "drop_off",
        "scheduled_pickup",
        "actual_pickup",
      ],
      items: [],
      // filteredItems: [],
      selectedItems: [],
      startDate: "",
      endDate: "",
      wardGroupFilter: "",
      tempThrdFilter: "99.2",
      enableTempThrdFilter: false,
      includeExpiredFilter: false,
      filterOn: ["group", "temperature"],
      numTodayRecords: 0,
      dismissSecs: 5,
        dismissCountDown: 0
    };
  },
  mounted() {
    console.log(`CC-Record mounted!`);
    this.startDate = moment().format(DATE_FORMAT);
    this.endDate = moment().format(DATE_FORMAT);

    var activeUser = this.$store.state.activeUser;
    if (activeUser != null) {
      console.log(`CC-Record mounted!, instId - ${activeUser.institution_id}`);
      this.instId = activeUser.institution_id;
    }
    var _this = this
    this.getCCRecordsFromDb((items) => {
      _this.showAlert()
      _this.numTodayRecords = items.length
    });
  },

  computed: {
    disableSingleAction() {
      return this.selectedItems.length != 1;
    },
    disableManyAction() {
      return this.selectedItems.length == 0;
    },
    disableCheckout() {
      return true;
    },
    numRows() {
      return this.items.length;
    },
    filter() {
      return this.wardGroupFilter + " " + this.tempThrdFilter;
    },
    hasDateRanges() {
      return this.startDate.length > 0 && this.endDate.length > 0;
    },
  },

  methods: {
    countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },
      showAlert() {
        this.dismissCountDown = this.dismissSecs
      },
    mapCCRecordToItem(ccRecord) {
      // map CCRecord to item
      var checkInTime = moment(ccRecord.check_in_event.time);
      var scheduledCheckOutTime = moment(ccRecord.check_out_scheduled_at);
      var checkOutTime = moment(ccRecord.check_out_event.time);

      return {
        _id: ccRecord._id,
        ward_id: ccRecord.ward_id,
        date: checkInTime.format(DATE_FORMAT_DISP),
        has_expired: ccRecord.has_expired,
        student_name: ccRecord.ward_info.name,
        group: ccRecord.ward_info.group,
        guardian_name: ccRecord.check_in_event.guardian_info.name,
        temperature: ccRecord.temperature,
        phone_number: ccRecord.check_in_event.guardian_info.phone_num,
        drop_off: checkInTime.format(TIME_FORMAT),
        scheduled_pickup: scheduledCheckOutTime.format(TIME_FORMAT),
        actual_pickup: checkOutTime.format(TIME_FORMAT),
      };
    },

    onRowSelected(items) {
      // handle "undefined" error when clicking the same item multiple times
      if (items[0] === undefined) return;
      this.selectedItems = items;
    },
    // Button Handlers
    onRefresh() {
      this.getCCRecordsFromDb(null);
    },
    onViewFamily() {
      this.getFamilyByWardIdFromDb(this.selectedItems[0], (family) => {
        if (family) {
          this.$store.commit("setLoadedFamily", family);
          this.$router.push("./families/info");
        }
      })
    },
    onCheckOutEvents() {},
    onDeleteEvents() {
      for (const item of this.selectedItems) {
        this.deleteCCRecordFromDb(item);
      }
      this.getCCRecordsFromDb(null);
    },
    onClearSelect() {
      this.selectedItems = [];
      this.$refs.ccRecordTable.clearSelected();
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
      this.onClearSelect();
    },
    onApplyDateRangeFilter() {
      this.currentPage = 1;
      this.onClearSelect();
      this.getCCRecordsFromDb(null);
    },
    filterFunction(row) {
      // console.log(`row: ${JSON.stringify(row)}, filterString: ${filterString}`)
      var expiredPred = false;
      if (!this.includeExpiredFilter) {
        expiredPred = row.has_expired;
      }
      var tempPred = true;
      if (this.enableTempThrdFilter) {
        tempPred = false;
        if (this.tempThrdFilter.length > 0) {
          tempPred = row.temperature > parseFloat(this.tempThrdFilter);
        }
      }
      var groupPred = true;
      if (this.wardGroupFilter.length > 0) {
        groupPred = row.group.toLowerCase().includes(this.wardGroupFilter);
      }
      // console.log(
      //   `expiredPred: ${expiredPred}, tempPred: ${tempPred}, groupPred: ${groupPred}, `
      // );
      return !expiredPred && tempPred && groupPred;
    },
    getCCRecordsFromDb(callback) {
      // console.log("CUSTOMER_TABLE: customers: " + JSON.stringify(this.customers));
      var _this = this;
      //// getting CC-Records from database
      const queryParams = { instID: this.instId };
      if (this.startDate.length > 0) {
        queryParams.startDate = moment(this.startDate)
          .hour(0)
          .minute(0)
          .second(0)
          .format();
      }
      if (this.endDate.length > 0) {
        queryParams.endDate = moment(this.endDate)
          .hour(23)
          .minute(59)
          .second(59)
          .format();
      }
      const queryArgs = queryString.stringify(queryParams);

      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "cc-records?" + queryArgs;
      console.log(`CC-Records: getCCRecord query -  ${query}`);
      http.open("GET", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // console.log(this.responseText);
          //// if data received with no error, add to table
          _this.ccRecords = JSON.parse(this.responseText).data;
          if (_this.ccRecords) {
            _this.items = _this.ccRecords.map((ccRecord) => {
              return _this.mapCCRecordToItem(ccRecord);
            });
            if (callback != null) {
              callback(_this.items)
            }
          }
          // console.log("Record_Table: items: " + JSON.stringify(this.items));
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
    getFamilyByWardIdFromDb(item, callback) {
      // var _this = this;
      const queryParams = {wardID: item.ward_id}
      const queryArgs = queryString.stringify(queryParams)
      const http = new XMLHttpRequest();
      const query = config.API_LOCATION + "family?" + queryArgs;
      console.log(`CC-Records: getCCRecord query -  ${query}`);
      http.open("GET", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // console.log(this.responseText);
          //// if data received with no error, add to table
          var family = JSON.parse(this.responseText).data;
          if (family && (callback != null)) {
            callback(family)
          }
          // console.log("Record_Table: items: " + JSON.stringify(this.items));
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
    deleteCCRecordFromDb(item) {
      // Send a Delete Request per each of selected Items
      var _this = this;
      const http = new XMLHttpRequest();
      var query = config.API_LOCATION + "cc-record/" + item._id;
      console.log(`CC-Records: delete CCRecord query -  ${query}`);
      http.open("DELETE", query, true);

      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // console.log(this.responseText);
          var response = JSON.parse(this.responseText);
          
          if (response.message != undefined) {
            _this.$bvToast.toast(response.message, {
              title: "DataBase Message",
              autoHideDelay: 2000,
            });
            // console.log("Record_Table: items: " + JSON.stringify(this.items));
          }
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send();
      } catch (e) {
        alert(e);
      }
    },
  },
};
</script>
