<template>
  <b-container>
    <b-row class="ml-2 mt-4" style="margin-bottom: 30px;" align-h="between">
      <b-col xl="3" lg="4">
        <h2>Add Institution</h2>
      </b-col>
      <b-col xl="3" lg="4">
        <b-button @click="$router.push('./add-admin')" variant="info">Create Admin</b-button>
      </b-col>
    </b-row>
    <b-row class="ml-4 mr-4">
      <b-col lg="4">
        <b-form @submit="onSubmit">
          <b-form-group id="input-group-1" label="Name" label-for="name-input">
            <b-form-input id="name-input" v-model="form.name" placeholder="Enter name" required></b-form-input>
          </b-form-group>
          <b-form-group id="input-group-2" label="Address" label-for="address-input">
            <b-form-input id="address-input" v-model="form.address" placeholder="Enter address" required></b-form-input>
          </b-form-group>
          <b-form-group id="input-group-3" label="State" label-for="state-input">
            <b-form-input id="state-input" v-model="form.state" placeholder="Enter state" required></b-form-input>
          </b-form-group>
          <b-form-group id="input-group-4" label="Zip Code" label-for="zip-code-input">
            <b-form-input id="zip-code-input" v-model="form.zipCode" placeholder="Enter Zip Code" required></b-form-input>
          </b-form-group>
          <b-form-group id="input-group-5" label="Type" label-for="type-select">
            <b-form-select id="type-select" v-model="form.type" :options="typeOptions" required></b-form-select>
          </b-form-group>
          <b-button type="submit" variant="primary">Create</b-button>
        </b-form>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import config from "../config";
export default {
  name: "AddInstitution",
  data() {
    return {
      form: {
        name: "",
        address: "",
        state: "",
        zipCode: "",
        type: "",
      },
      typeOptions: [
        { value: 0, text: "School" },
        { value: 1, text: "Hospital" },
        { value: 2, text: "Others" },
      ],
    };
  },

  methods: {
    onSubmit(event) {
      var _this = this;
      event.preventDefault();
      this.createInstitutionInDb((message) => {
        _this.$bvToast.toast(message, {
          title: "DataBase Message",
          autoHideDelay: 2000,
        });
      });
    },
    createInstitutionInDb(callback) {
      const http = new XMLHttpRequest();
      var requestBody = {
        type: this.form.type,
        name: this.form.name,
        address: this.form.address,
        state: this.form.state,
        zip_code: this.form.zipCode,
      };
      const query = config.API_LOCATION + "institution";
      http.open("POST", query, true);
      http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      http.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 201) {
          var response = JSON.parse(this.responseText);
          if (response.message != undefined && callback != null) {
            callback(response.message);
          }
        } else if (this.readyState === 4) {
          alert(this.responseText);
        }
      };
      try {
        http.send(JSON.stringify(requestBody));
      } catch (e) {
        alert(e);
      }
    },
  },
};
</script>
